/* eslint-disable no-console */
import { ROOT_URL } from "./../../config"
import sio from 'socket.io-client'

export default {
    name: "Dashboard",
    data() {
        return {
            calledClients: {
                amount: 0,
                max: 10000,
            },
            totalCalls:{
                amount: 0,
                max: 10000,
            },
            contactedClients:{
                amount: 0,
                max: 10000,
            },
            wrongNumbers: {
                amount: 0,
                max: 500,
            },
            io: null,
            clients: [],
            calls: [],
            loading: true
        }
    },
    mounted: function () {
        this.$q.loadingBar.stop()

        let dbName = localStorage.getItem('entity_prefix')

        this.io = sio(`${ROOT_URL}`)
        
        this.io.on('connect', () => {
            this.io.emit('update', {key: 'all_clients', db: dbName})

            this.io.emit('update', {key: 'total_calls', db: dbName})
            this.io.emit('update', {key: 'called_clients', db: dbName})
            this.io.emit('update', {key: 'wrong_number', db: dbName})
            this.io.emit('update', {key: 'contacted_clients', db: dbName})

            setInterval(()=>{
                this.io.emit('update', {key: 'total_calls', db: dbName, limit: 15})
            }, 7000)

            setInterval(()=>{
                this.io.emit('update', {key: 'called_clients', db: dbName})
                this.io.emit('update', {key: 'contacted_clients', db: dbName})
                this.io.emit('update', {key: 'wrong_number', db: dbName})
            }, 30000)

        })

        this.io.on('update-front', (response) => {
            if(response.status){
                switch(response.key){
                    case 'total_calls':
                        this.joinCalls(response.data.rows, response.limit)
                        break
                    case 'called_clients':
                        this.calledClients.amount = response.data.rows
                        break
                    case 'contacted_clients':
                        this.contactedClients.amount = response.data.rows
                        break
                    case 'wrong_number':
                        this.wrongNumbers.amount = response.data.rows
                        break
                    case 'all_clients':
                        this.calledClients.max = response.data.rows
                        this.contactedClients.max = response.data.rows
                        this.totalCalls.max = response.data.rows * 5
                        break
                }
            }
        })
    },
    methods:{
        joinCalls(rows, limit){
            let newData = []
            if(this.calls.length){
                let tmp = [...this.calls]
                let r = tmp.splice(-1*limit,limit)
                for(let index in rows){
                    let exist = r.filter(el => el.id == rows[index].id)
                    if(!exist.length){
                        newData.push(rows[index])
                    }
                }
                r = r.concat(newData)
                this.calls = tmp.concat(r).sort((a,b)=>a.id - b.id)
                
            }else{
                this.calls = rows.sort((a,b)=>a.id - b.id)
            }

            this.totalCalls.amount = this.calls.length
            this.loading = false
        }
    },
    beforeDestroy: function() {
        if(this.io != null){
            this.io.disconnect()
        }
    }
}